/* font-family: 'Muli', sans-serif; */
:root {
  font-size: 20px;
  --ffMain: 'Muli', sans-serif;
}

* {
  margin: 0;
  padding: 0;
  cursor: default;
  box-sizing: border-box;
}

body {
  max-width: 1920px;
  margin: 0 auto;
  background-color: #777;
  background-image: url(./ORG/bgi.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
}

@media (max-width: 1600px) {
  :root {
    font-size: 17px;
  }
}

@media (max-width: 1366px) {
  :root {
    font-size: 15px;
  }
}

@media (max-width: 1280px) {
  :root {
    font-size: 14px;
  }
}

@media (max-width: 980px) {
  :root {
    font-size: 12px;
  }
}

@media (max-width: 600px) {
  :root {
    font-size: 10px;
  }
}

/* INDEX.HTML */
.main {
  position: relative;
  min-height: 82vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  overflow: hidden;
}

nav {
  height: 10vh;
  min-height: 10vh;
  width: 100%;
  overflow: hidden;
}

nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 95%;
  list-style: none;
}

nav ul li a {
  display: block;
  text-decoration: none;
  font-family: var(--ffMain);
  font-weight: bold;
  color: #fff;
  font-size: 1.3rem;
  text-shadow: 1px -1px 2px black;
  margin-right: 1vw;
  padding: 0 10px;
  cursor: pointer;
  transition: .1s;
}

nav ul li a:hover,
nav ul li a.active {
  color: black;
  background-color: #fff;
  text-shadow: none;
  box-shadow: 0 2px 4px 5px white;
  border-radius: 3%;
}

.main h1 {
  position: absolute;
  top: 20vh;
  left: 10%;
  color: chartreuse;
  text-shadow: 2px -2px 3px black;
  font-size: 4rem;
  font-family: 'Muli', sans-serif;
  text-transform: uppercase;
  transform: scale(1);
  letter-spacing: 3px;
  animation: textScale 10s infinite linear;
}

@media (max-width: 450px) {
  .main h1 {
    top: 20vh;
    font-size: 3.2rem;
  }

  .main nav ul li {
    margin-right: .1vw;
  }
}

@keyframes textScale {
  0% {
    transform: scale(1);
  }

  30% {
    transform: scale(1);
  }

  55% {
    transform: scale(1.2);
  }

  75% {
    transform: scale(1.2);
  }

  80% {
    transform: scale(1);
  }

  100% {
    transform: scale(1);
  }

}

.main div.greenSquare {
  position: absolute;
  right: -8vw;
  top: 7vh;
  width: 16vw;
  height: 16vw;
  transform: rotate(45deg);
  background-image: linear-gradient(45deg, chartreuse 25%, darkgreen 80%);
  text-align: right;
  border: 1px solid black;
}

.main div.logo img {
  position: absolute;
  bottom: 7vh;
  right: 20%;
  height: 50%;
}

@media (max-width: 500px) {
  .main div.greenSquare {
    top: 10vh;
    right: -7vw;
    width: 14vw;
    height: 14vw;
  }

  .main div.logo img {
    right: 5%
  }
}

section.contact {
  width: 100%;
  font-size: 1.2rem;
  font-family: var(--ffMain);
  padding: 2vh 1vw;
}

section.contact h2 {
  text-align: center;
  padding: 5vh 0;
  color: black;
}

section.contact div.contact {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2vh 0;
  font-size: 1.3rem;
}

section.contact div.contact div {
  flex-basis: 50%;
  padding: 5vh 2vw;
  color: white;
  text-shadow: 1px -1px 2px black;
}

section.contact div.contact div:nth-child(1) {
  text-align: right;
}

/* LOGO MA≈ÅE*/
section.contact div img {
  height: 20vh;
  padding: 0 0 1vh 0;
}

section.contact div.icons {
  width: 100%;
}

section.contact div.icons p {
  color: white;
  text-shadow: 1px -1px 2px black;
  padding: 2vh 0;
}

section.contact div p i {
  font-size: 30px;
  padding: 1vh;
  color: limegreen;
  vertical-align: -15%;
  transition: .1s;
}

section.contact div p:hover i {
  color: rgb(94, 255, 45);
}

section.contact p a {
  text-decoration: none;
  color: white;
  text-shadow: 1px -1px 2px black;
}

@media (max-width: 660px) {
  section.contact {
    font-size: 1.4rem;
  }

  section.contact div.contact {
    flex-direction: column;
  }

  section.contact div.contact div:nth-child(1) {
    text-align: center;
  }

  section.contact div.icons {
    text-align: center;
  }
}

/* FOOTER */
footer {
  min-height: 8vh;
  display: flex;
  justify-content: center;
  border-top: 1px solid black;
  font-family: var(--ffMain);
  background-color: #333;
  font-size: 1rem;
}

footer p {
  color: white;
  padding: 10px 0;
}

footer p span.reg {
  margin-left: 10px;
  cursor: pointer;
}

@media (max-width: 480px) {
  footer {
    flex-direction: column;
    align-items: center;
  }
}

/* ABOUT.HTML */
/* PRICELIST.HTML */
/* SERVICES.HTML */
section.about,
section.price,
section.contact {
  min-height: 82vh;
}

section.about div,
section.price div,
section.services div,
section.contact div {
  width: 100%;
  min-height: 11vh;
  padding-top: 3vh;
  padding-bottom: 3vh;
}

section.about div h1.h1about,
section.price div h1.h1price,
section.price div h1.h1sales,
section.services div h1.h1services1,
section.services div h1.h1services2,
section.contact h1.h1contact {
  text-shadow: 2px -2px 3px black;
  font-size: 2.5rem;
  font-family: 'Muli', sans-serif;
  text-transform: uppercase;
  color: chartreuse;
  animation: h1Entry 1s both;
  text-align: center;
}

section.price div h1.h1sales {
  color: orangered;
}

section.about div.textAbout,
section.price div.priceList,
section.services div.textServices {
  width: 80%;
  height: auto;
  margin: 0 auto;
  padding: 2vh 0;
}

section.about div p,
section.price div.priceList p,
section.services div.textServices p {
  line-height: 1.3;
  font-size: 1.3rem;
  color: white;
  font-family: var(--ffMain);
  padding-bottom: 3%;
  text-shadow: 1px -1px 2px black;
}

section.about div p span,
section.price div.priceList p span,
section.services div.textServices p span {
  display: inline-block;
  width: 2vh;
  height: 2vh;
  border-radius: 50%;
  margin-left: -3%;
  margin-right: 3%;
  background-color: lime;
  box-shadow: 0 0 0 3px dimgray, 0 0 0 5px chartreuse;
}

section.price div.priceList p {
  padding-bottom: 1.5%;
}

/* +SERVICES.HTML */
section.services div.imgServices {
  height: auto;
  text-align: center;
}

section.services div.imgServices img {
  border-radius: 1%;
  box-shadow: 0 0 5px 1px black;
  width: 75%;
}

footer.footerSec {
  border: none;
  padding: 3vh;
}

/* +Sales.HTML */
section.price div.promo {
  width: 80%;
  height: auto;
  margin: 0 auto;
}

section.price div.promo p {
  line-height: 1.8rem;
  font-size: 1.8rem;
  color: chartreuse;
  font-family: var(--ffMain);
  padding-bottom: 3%;
  text-shadow: 2px -2px 2px black;
  text-align: center;
  font-weight: bold;
}

section.price div.imgPromo {
  position: relative;
  height: auto;
  text-align: center;
  padding-bottom: 5vh;
}

section.price div.imgPromo img:nth-of-type(1) {
  border-radius: 2%;
  box-shadow: 0 0 5px 1px black;
  width: 40%;
}

section.price div.imgPromo img:nth-of-type(2) {
  position: absolute;
  top: 0;
  right: 26%;
  width: 15%;
}

.priceList table {
  background-color: #eee;
  text-align: center;
  width: 100%;
  border-collapse: collapse;
  font-family: var(--ffMain);
}

.priceList table thead {
  background-color: #bbb;
}

.priceList table tbody tr:nth-of-type(2),
.priceList table tbody tr:nth-of-type(4) {
  background-color: #ddd;
}

.priceList table td,
.priceList table th {
  padding: .75rem;
  border: 1px solid #dee2e6;
}

.priceList table td:hover {
  background-color: greenyellow;
}